import React from 'react';
import {hot} from "react-hot-loader";
import {List, ListItem, Page} from "react-onsenui";
import {getLang} from "@web3game/lang";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {watchAccountChange} from "../../../common/utils/watchAccountChange";
import {createNoBackHeaderRender} from "../Header";
import {getRoutes} from "../../routes/getRoutes";

const getAccountPageList = () => [
    getRoutes().LanguageSelector,
    getRoutes().MyEarning,
    getRoutes().TradingLog,
    getRoutes().Invitation,
    getRoutes().SystemAnnouncement,
    getRoutes().AboutUS,
];

export type MyAccountPageProps = {
    pushPage: (router: any) => any;
};

function MyAccountPagePure({ pushPage }: MyAccountPageProps) {

    return (
        <Page renderFixed={createNoBackHeaderRender(getLang().MY)}>
            <PagePadder />
            <List
                modifier="inset"
                dataSource={getAccountPageList()}
                renderRow={({ title, key, component }, idx) => (
                    <ListItem
                        key={idx}
                        modifier="chevron"
                        onClick={() => pushPage(
                            {
                                key,
                                component,
                            },
                        )}
                    >
                        <div className="center">{title}</div>
                    </ListItem>
                )}
            />
        </Page>
    );
}

export const MyAccountPage = compose<MyAccountPageProps>(
    hot(module),
    React.memo,
    watchAccountChange,
)(MyAccountPagePure);

export const renderMyAccountTabPage = ({ pushPage }: MyAccountPageProps) => (
    <MyAccountPage key="myAccount" pushPage={pushPage} />
);
